"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  ArrowPathIcon,
  FunnelIcon,
  ExclamationTriangleIcon,
} from "@heroicons/react/24/outline";

type ComparisonItem = {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: "LOW" | "MEDIUM" | "HIGH" | string | null;
  changes_count?: number;
};

type Risk = "HIGH" | "MEDIUM" | "LOW";

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() || "http://127.0.0.1:8000";

/* ---------------- utils ---------------- */

function normalizeRisk(r?: string | null): Risk {
  const x = (r || "").toUpperCase();
  if (x.includes("HIGH")) return "HIGH";
  if (x.includes("MEDIUM")) return "MEDIUM";
  return "LOW";
}

function formatDate(iso: string) {
  try {
    return new Date(iso).toLocaleString("th-TH", {
      month: "short",
      day: "numeric",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  } catch {
    return iso;
  }
}

function formatMonthKey(d: Date) {
  return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, "0")}`;
}

function monthLabel(key: string) {
  const [y, m] = key.split("-").map(Number);
  return new Date(y, m - 1, 1).toLocaleDateString("th-TH", {
    month: "long",
    year: "numeric",
  });
}

/* ---------------- UI ---------------- */

function RiskBadge({ r }: { r: Risk }) {
  if (r === "HIGH")
    return (
      <span className="rounded-full bg-red-100 px-2 py-0.5 text-xs font-bold text-red-700">
        HIGH
      </span>
    );
  if (r === "MEDIUM")
    return (
      <span className="rounded-full bg-yellow-100 px-2 py-0.5 text-xs font-bold text-yellow-700">
        MEDIUM
      </span>
    );
  return (
    <span className="rounded-full bg-green-100 px-2 py-0.5 text-xs font-bold text-green-700">
      LOW
    </span>
  );
}

/* ---------------- Page ---------------- */

export default function ReportsPage() {
  const [items, setItems] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const [monthKey, setMonthKey] = useState(formatMonthKey(new Date()));
  const [docFilter, setDocFilter] = useState("ALL");
  const [riskFilter, setRiskFilter] = useState<"ALL" | Risk>("ALL");

  const fetchData = async () => {
    setLoading(true);
    setError(null);
    try {
      const res = await fetch(`${API_BASE}/comparisons?limit=200`, {
        cache: "no-store",
      });
      if (!res.ok) throw new Error("โหลดข้อมูลไม่สำเร็จ");
      const data = await res.json();
      setItems(Array.isArray(data) ? data : []);
    } catch (e: any) {
      setError(e.message || "เกิดข้อผิดพลาด");
      setItems([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const computed = useMemo(() => {
    const list = [...items].sort(
      (a, b) => +new Date(b.created_at) - +new Date(a.created_at)
    );

    const months = Array.from(
      new Set(list.map((x) => formatMonthKey(new Date(x.created_at))))
    ).sort((a, b) => (a < b ? 1 : -1));

    const monthItems = list.filter(
      (x) => formatMonthKey(new Date(x.created_at)) === monthKey
    );

    const docs = [
      "ALL",
      ...Array.from(new Set(monthItems.map((x) => x.document_name))),
    ];

    const filtered = monthItems.filter((x) => {
      if (docFilter !== "ALL" && x.document_name !== docFilter) return false;
      if (riskFilter !== "ALL" && normalizeRisk(x.overall_risk_level) !== riskFilter)
        return false;
      return true;
    });

    return { months, docs, filtered };
  }, [items, monthKey, docFilter, riskFilter]);

  return (
    <div className="min-h-screen bg-slate-100 py-6">
      <div className="mx-auto max-w-3xl space-y-4">
        {/* Header */}
        <div className="rounded-2xl bg-white p-4 shadow-sm">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-lg font-extrabold text-slate-900">Reports</h1>
              <p className="text-sm font-medium text-slate-600">
                ประวัติการเปรียบเทียบเอกสาร
              </p>
            </div>
            <button
              onClick={fetchData}
              className="inline-flex items-center gap-2 rounded-full bg-blue-600 px-4 py-2 text-sm font-bold text-white hover:bg-blue-700"
            >
              <ArrowPathIcon
                className={`h-4 w-4 ${loading ? "animate-spin" : ""}`}
              />
              Refresh
            </button>
          </div>

          {error && (
            <div className="mt-3 flex items-start gap-2 rounded-xl bg-red-50 p-3 text-sm text-red-700">
              <ExclamationTriangleIcon className="h-5 w-5" />
              {error}
            </div>
          )}
        </div>

        {/* Filters */}
        <div className="rounded-2xl bg-white p-4 shadow-sm">
          <div className="mb-3 flex items-center gap-2 text-sm font-bold text-slate-700">
            <FunnelIcon className="h-4 w-4" />
            Filters
          </div>

          <div className="grid grid-cols-1 gap-2 sm:grid-cols-3">
            <select
              value={monthKey}
              onChange={(e) => {
                setMonthKey(e.target.value);
                setDocFilter("ALL");
                setRiskFilter("ALL");
              }}
              className="rounded-xl border px-3 py-2 text-sm"
            >
              {computed.months.map((m) => (
                <option key={m} value={m}>
                  {monthLabel(m)}
                </option>
              ))}
            </select>

            <select
              value={docFilter}
              onChange={(e) => setDocFilter(e.target.value)}
              className="rounded-xl border px-3 py-2 text-sm"
            >
              {computed.docs.map((d) => (
                <option key={d} value={d}>
                  {d === "ALL" ? "All documents" : d}
                </option>
              ))}
            </select>

            <select
              value={riskFilter}
              onChange={(e) => setRiskFilter(e.target.value as any)}
              className="rounded-xl border px-3 py-2 text-sm"
            >
              <option value="ALL">All risk</option>
              <option value="HIGH">HIGH</option>
              <option value="MEDIUM">MEDIUM</option>
              <option value="LOW">LOW</option>
            </select>
          </div>
        </div>

        {/* Feed */}
        {loading ? (
          <div className="py-10 text-center text-sm text-slate-600">
            กำลังโหลด...
          </div>
        ) : computed.filtered.length === 0 ? (
          <div className="py-10 text-center text-sm text-slate-600">
            ไม่พบข้อมูล
          </div>
        ) : (
          computed.filtered.map((x) => (
            <div
              key={x.id}
              className="rounded-2xl bg-white p-4 shadow-sm"
            >
              <div className="flex items-start justify-between gap-3">
                <div className="min-w-0">
                  <div className="flex items-center gap-2">
                    <div className="truncate font-extrabold text-slate-900">
                      {x.document_name}
                    </div>
                    <RiskBadge r={normalizeRisk(x.overall_risk_level)} />
                  </div>

                  <div className="mt-1 text-sm text-slate-600">
                    {x.version_old_label} → {x.version_new_label}
                  </div>

                  <div className="mt-1 text-xs text-slate-500">
                    {formatDate(x.created_at)} •{" "}
                    {x.changes_count ?? 0} changes
                  </div>
                </div>

                <Link
                  href={`/compare/${x.id}`}
                  className="shrink-0 rounded-full bg-blue-600 px-4 py-2 text-sm font-bold text-white hover:bg-blue-700"
                >
                  Open
                </Link>
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
}
